@font-face {
  font-family: "CodeProBoldLC";
  src: url("../fonts/CodeProBoldLC.otf") format("opentype");
}
@font-face {
  font-family: "BaseticaPro-Med";
  src: url("../fonts/BaseticaPro-Med.otf") format("opentype");
}
@font-face {
  font-family: "CamingoDosProCd-SemiBold";
  src: url("../fonts/CamingoDosProCd-SemiBold.otf") format("opentype");
}
@font-face {
  font-family: "Gotham-Bold";
  src: url("../fonts/Gotham-Bold.otf") format("opentype");
}
header {
  background-color: transparent;
}

nav {
  height: 15rem;
  margin: 0 10rem;
}

.container-fluid div a {
  color: #02490b;
  font-weight: bold;
}
.container-fluid div .navbar-right {
  font-weight: bold;
}
.container-fluid div .navbar-center a {
  margin: 0 1.5rem;
  border-bottom: 3px solid transparent;
  transition: all 0.5s;
}
.container-fluid div .navbar-center a:hover {
  border-bottom: 3px solid #02490b;
}
.container-fluid div button {
  background-color: #02490b;
  color: #ffffff;
  font-weight: bold;
  padding: 1.75rem 1.5rem;
  border-radius: 1rem;
}
.container-fluid div a, .container-fluid div button {
  font-size: 1.5rem;
}
@media (min-width: 481px) {
  .container-fluid div a, .container-fluid div button {
    font-size: 1.75rem;
  }
}
@media (min-width: 768px) {
  .container-fluid div a, .container-fluid div button {
    font-size: 2rem;
  }
}
@media (min-width: 1025px) {
  .container-fluid div a, .container-fluid div button {
    font-size: 2.5rem;
  }
}

.navbar-nav .nav-link.active {
  color: #02490b;
}